	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"McDonald's Versus","date":"23 Jul 2009","slug":"/2009-07-23-mcdonalds-versus/","tags":"Y2009"},"html":"<p>This starts out as an ordinary McDonald's spot, but there's a surprise at about 28 seconds.</p>\n<p><object width=\"400\" height=\"320\"><param name=\"allowfullscreen\" value=\"true\" /><param name=\"allowscriptaccess\" value=\"always\" /><param name=\"movie\" value=\"http://vimeo.com/moogaloop.swf?clip_id=5614632&amp;server=vimeo.com&amp;show_title=1&amp;show_byline=1&amp;show_portrait=0&amp;color=&amp;fullscreen=1\" /><embed src=\"http://vimeo.com/moogaloop.swf?clip_id=5614632&amp;server=vimeo.com&amp;show_title=1&amp;show_byline=1&amp;show_portrait=0&amp;color=&amp;fullscreen=1\" type=\"application/x-shockwave-flash\" allowfullscreen=\"true\" allowscriptaccess=\"always\" width=\"400\" height=\"320\" /></object></p>\n<p><a href=\"http://vimeo.com/5614632\">McDonald's Versus</a> from <a href=\"http://vimeo.com/user1349603\">Bruce Branit</a> on <a href=\"http://vimeo.com\">Vimeo</a>.</p>\n<p>(via <a href=\"http://www.kottke.org/09/07/virtually-delicious\">kottke</a>)</p>"},"next":{"fields":{"title":"Happy Fourth","date":"06 Jul 2009","slug":"/2009-07-06-happy-fourth/"}},"previous":{"fields":{"title":"Liberty's Crown","date":"24 Jul 2009","slug":"/2009-07-24-libertys-crown/"}}},{"node":{"fields":{"title":"Happy Fourth","date":"06 Jul 2009","slug":"/2009-07-06-happy-fourth/","tags":"Y2009"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3691970158\" title=\"View '040709' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2598/3691970158_61fb8eb1de.jpg\" alt=\"040709\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>Happy 4th of July weekend from Starbuck, Minnesota! We'll be back when we're done relaxing...</p>\n<p>P.S. We splurged a bit on the rental car.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3691228387\" title=\"View 'IMG_0192' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2442/3691228387_cf43b2dd2a_m.jpg\" alt=\"IMG_0192\" border=\"0\" width=\"240\" height=\"180\" /></a></p>"},"next":{"fields":{"title":"Interesting Drive","date":"27 Jun 2009","slug":"/2009-06-27-interesting-drive/"}},"previous":{"fields":{"title":"McDonald's Versus","date":"23 Jul 2009","slug":"/2009-07-23-mcdonalds-versus/"}}},{"node":{"fields":{"title":"Interesting Drive","date":"27 Jun 2009","slug":"/2009-06-27-interesting-drive/","tags":"Y2009"},"html":"<p>Your drive on the Autobahn A7 southbound toward Hannover between HH-Marmstorf and Seevetal-Fleestedt has just gotten more interesting...</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3664605356\" title=\"View 'Directional A7' on Flickr.com\"><img src=\"http://farm4.static.flickr.com/3560/3664605356_a37c888b51.jpg\" alt=\"Directional A7\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>We have something even grander planned for the northbound side. Stay tuned.</p>"},"next":{"fields":{"title":"Easy Morning For Free Again!","date":"18 Jun 2009","slug":"/2009-06-18-easy-morning-for-free-again/"}},"previous":{"fields":{"title":"Happy Fourth","date":"06 Jul 2009","slug":"/2009-07-06-happy-fourth/"}}}],"pathPrefix":"","first":false,"last":false,"index":55,"pageCount":1101}}

